<template>
  <footer>
    <div class="footer_top" :class="{ hideFoot: !showFooter }">
      <div>
        <ul class="school">
          <li>
            <a
              href="https://www.tbs-education.fr/professeur/barbarossa-camilla/"
              target="_blank"
            >
              <img src="../assets/img/tbs-logoetbaseline.png" alt="logo tbs" />
            </a>
          </li>
        </ul>
      </div>
      <div>
        <ul>
          <li>
            <a
              href="https://www.linkedin.com/in/camilla-barbarossa-01422a40/"
              target="blank"
              ><i class="fab fa-linkedin-in"></i
            ></a>
          </li>
          <li>
            <a
              href="https://www.researchgate.net/profile/Camilla-Barbarossa"
              target="blank"
              ><i class="fab fa-researchgate"></i
            ></a>
          </li>
        </ul>
      </div>
    </div>
    <div class="footer_bottom">
      <div>
        <!--   <img src="../assets/img/cb.png" alt="" />  -->
        <span class="name"> camilla barbarossa </span>
      </div>
      <div>
        <span class="powered">
          powered by
          <a href="https://github.com/gabrisca" target="_blank"> gabrisca </a>
        </span>
      </div>
    </div>
  </footer>
</template>

<script>
export default {
  // preparo Footer per essere esportato
  name: "FooterView",
  data() {
    return {
      showFooter: false,
      lastScrollPosition: 0,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.onScroll);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.onScroll);
  },
  methods: {
    // funzione che nasconde il menu allo scrollDown e lo fa riapparire allo scrollUp (solo sopra width 800)
    onScroll() {
      var viewport_width = window.innerWidth;
      if (viewport_width > 800) {
        // prendo il valore corrente di currentScrollPosition
        const currentScrollPosition = window.pageYOffset;
        if (currentScrollPosition < 10) {
          return;
        }
        // Stoppa l'esecuzione della funzione se la differenza tra currentScrollPosition e lastScrollPosition è minore dell'offset (10)
        if (Math.abs(currentScrollPosition - this.lastScrollPosition) < 0) {
          return;
        }
        this.showFooter = currentScrollPosition > this.lastScrollPosition;
        // Setto il valore di currentScrollPosition con il valore di lastScrollPosition
        this.lastScrollPosition = currentScrollPosition;
      } else {
        this.showFooter = false;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
/* // importo gli stili */
@import "../assets/style/app.scss";
footer {
  @include size($width: 100%, $height: 150px);
  position: fixed;
  z-index: 998;
  bottom: 0;
  height: 150px;
  @media all and (max-width: 600px) {
    height: 50px;
  }
  @media all and (min-width: 600px) and (max-width: 601px) {
    height: 150px;
  }
  .footer_top {
    height: 100px;
    transition: transform 0.2s ease;
    position: relative;
    z-index: 997;
    box-shadow: 0px 0px 8px rgba(4, 4, 4, 0.6);
    @media all and (max-width: 940px) {
      display: none;
    }
    @include flex($justify-content: center, $align-items: center);
    @include background-gradient(
      rgba(22, 22, 22, 0.63),
      $color-primary,
      vertical
    );
    &.hideFoot {
      transform: translateY(150px);
    }
    ul {
      @include flex($align-items: center);
      li {
        margin-right: 15px;
        font-size: 1.5rem;
        a {
          color: $brand-color;
          &:hover{
            color: #007bff;
          }
          img {
            transition: all 0.8s;
            height: 50px;
            &:hover {
              @include filter(brightness, 1.2);
            }
          }
        }
      }
      &.school {
        display: none;
        @include breakpoint(tablet) {
          display: block;
        }
      }
    }
    @include breakpoint(tablet) {
      @include flex($justify-content: space-around);
    }
  }
  .footer_bottom {
    height: 50px;
    background-color: $color-primary;
    position: relative;
    z-index: 998;
    box-shadow: 0px 0px 8px rgba(4, 4, 4, 0.6);
    @include flex($flex-direction: column, $justify-content: center);
    @media all and (max-width: 799px) {
      box-shadow: 0px 0px 8px rgba(4, 4, 4, 0.6);
    }
    div {
      height: 100%;
      //margin-right: 5px;
      @include flex($justify-content: center, $align-items: center);
      span {
        color: $brand-color;
        &.name {
          text-transform: uppercase;
          font-size: 0.9rem;
          margin: 0 auto;
          @include breakpoint(tablet) {
            &::after {
              content: " \2042";
              padding: 3px;
            }
          }
        }
        &.powered {
          font-size: 0.8rem;
          @include breakpoint(tablet) {
            margin-top: 0;
            margin-left: 0;
            font-size: 1rem;
          }
        }
      }
      /*
      img {
        height: 50%;
        @include breakpoint(smartphone) {
          margin-left: -25px;
              }
      }
      */
    }
    @include breakpoint(tablet) {
      @include flex(
        $flex-direction: row,
        $justify-content: center,
        $align-items: center
      );
    }
  }
}
</style>